import '../styles/index.scss';

import BeCoMap from './map/map.js';
import Route from './models/route/model.js';
import * as data from './map/data/graph.json';


//TODO https://medium.com/4thought-studios/documenting-javascript-projects-f72429da2eea
//https://gomakethings.com/whats-the-best-way-to-document-javascript/

var map;

/**
 * This will set the DEBUG config to the window Object. Used only for BeCo Internal Testing
*/
window.GLOBALS  = {
	DEBUG : DEBUG
};

/**
 * Initialise the map class. This method is called via javascript Bridge by Android/iOS
 * 
 * @param {Object} Floor Object
 * @param {params} Map properties
 */
window.initMap = function(floors,params) {
	map = new BeCoMap(data,floors,params);
	map.init();
	//TODO load other layers after image loading completed
	//map.addMarkers(geojsonObject);
	
	map.initializeWhereAmI();
	// map.setWhereAmI(800,300);
	//map.updatePosition([1500, 800]);

};

/**
 * Set current user position 
 * 
 * @param  {Number} x X co-ordinate
 * @param  {Number} y Y co-ordinate
 */
window.BESetCurrentPosition = function(x,y) {
	map.updatePosition([x, y]);
};

window.getMap = function(){
	return map.getMap();
};

/**
 * Set Labels for the map
 * 
 * @param  {Object} labelObj 
 */
window.BESetMapLabels = function(x,y) {
	map.addLabels();
};

/**
 * Set markers for the selected floors 
 * 
 * @param  {Object} geojsonObject geo json object
 */
window.BESetMarkers = function(geojsonObject) {
	map.addMarkers(geojsonObject);
};

/**
 * Change the marker of a particular Point to indicate that it is selected on the map
 * 
 * @param  Point Id
 */
window.BESelectPoint = function(id) {
	map.selectPoint(id);
};

/**
 * Change the Floor
 * 
 * @param  Floor Id
 */
window.BEChangeFloor = function(id) {
	map.clearRoute();
	map.showFloorTile(id);
};
/**
 * Clear the selected point marker
 * 
 */
window.BEClearSelectedPoint = function() {
	map.clearSelectedPoint();
};


/**
 * Draw the route preview on the map
 * 
 * @param {Array.{Array.<RouteObj>}} routeArray
 */
window.BEDrawRoutePreview = function(routeArray){
	map.drawRoutePreview(routeArray);
};


/**
 * Clear the route preview
 * 
 */
window.BEClearRoutePreview = function() {
	map.clearRoutePreview();
};

/**
 * Draw the route on the map
 * 
 * @param {Array.<Object>} nodeList
 */
window.BEDrawRoute = function(nodeList, coveredNodeList, arrowObject) {
	map.clearRoute();
	if(arrowObject) map.addArrow(arrowObject);
	if(nodeList) map.drawRoute(nodeList);
	if(coveredNodeList) map.drawCoveredRoute(coveredNodeList);
};


/**
 * Update covered route on the map
 * 
 * @param {Array.<Object>} coveredNodeList
 * @param {Array.<Object>} arrowObject
 */
window.BEUpdateRoute = function(coveredNodeList, arrowObject){
	if(coveredNodeList) map.drawCoveredRoute(coveredNodeList);
	if(arrowObject) map.addArrow(arrowObject);
};


/**
 * Clear the route
 * 
 */
window.BEClearRoute = function() {
	map.clearRoute();
};

/**
 * Rotate the map
 * 
 */
window.BERotate = function(angle, coordinates) {
	map.rotate(angle, coordinates);
};

/**
 * Reset Rotate the map
 * 
 */
window.BEResetAngle = function() {
	map.resetAngle();
};


/**
 * Focus to point
 * 
 */
window.BEFocusToCurrentPosition = function(x, y) {
	map.moveToPoint(x, y);
};

/**
 * Remove current point
 * 
 */
window.BERemoveCurrentPosition = function() {
	map.removeCurrentPosition();
};

/**
 * Set LocationMarker
 * 
 */
window.BESetLocationMarker = function(x, y, rotateAngle) {
	map.setLocationMarker(x, y,rotateAngle);
};

/**
 * Set Location Marker Angle
 * 
 */
window.BEChangeLocationMarkerAngle = function(rotateAngle) {
	map.changeDirection(rotateAngle);
};


/**
 * Remove Location marker
 * 
 */
window.BERemoveLocationMarker = function() {
	map.changeDirection(0);
	map.removeLocationMarker();
};







