import {Circle as CircleStyle, Fill, Stroke, Style} from 'ol/style.js';
import Feature from 'ol/Feature.js';
import Point from 'ol/geom/Point.js';
import Overlay from 'ol/Overlay.js';

export default class CurrentPosition {

	constructor() {
    	 this.name = 'CurrentPosition';
    	 this.positionFeature;
         this.whereAmI = new Overlay({
            positioning: 'center-center',
            element: document.getElementById('marker'),
            stopEvent: false
         });
         this.overlayDisabled;
	}

	toString() {
        return this.name;
    }


    style() {
        return new Style({
	        image: new CircleStyle({
	          	radius: 8,
	          	fill: new Fill({
	            	color: '#4285F4'
	         	}),
		        stroke: new Stroke({
		            color: '#DCE8FD',
		            width: 8
		        })
    		})
      	});
    }

    init(map,coordinates){
    	map.addOverlay(this.whereAmI);
        if(coordinates){
            this.whereAmI.setPosition(coordinates);
        }
    }

    setCurrentPoint(coordinates,map){
        if(this.overlayDisabled && this.whereAmI){
            map.addOverlay(this.whereAmI);
            this.overlayDisabled = false;
        }
    	this.whereAmI.setPosition(coordinates ?
          coordinates : null);
    }

    removeCurrentPoint(map){
        if(this.whereAmI != null){
            map.removeOverlay(this.whereAmI);
            this.overlayDisabled = true;
        }
    }
}

