import _ from 'underscore';
import Map from 'ol/Map.js';
import {defaults as defaultControls, Attribution} from 'ol/control.js';
import {Vector as VectorLayer} from 'ol/layer.js';
import VectorSource from 'ol/source/Vector.js';
import { Style, Icon, Fill } from 'ol/style.js';
import LineString from 'ol/geom/LineString';
import Point from 'ol/geom/Point';
import {getCenter} from 'ol/extent';
import {easeIn, easeOut} from 'ol/easing';

import Floors from '../models/floor/model.js';
import Route from '../models/route/model.js';
import Arrow from '../models/route/arrow.js';
import Feature from 'ol/Feature';
import Markers from '../models/marker/model.js';
import Image from '../models/marker/images.js';
import Label from '../models/label/model.js';
import CurrentPosition from '../models/whereami/model.js';
import { view, target, extent }  from './projection.js';

export default class BeCoMap {

	constructor(graphObject, floorData, params){
        this.floors = new Floors(floorData, params);
        this.markers = new Markers();
        this.labels = new Label();
        this.whereami = new CurrentPosition();
        this.map = null;
        this.vectorMarkerLayer = null;
        this.vectorLabelLayer = null;
        this.vectorRouteLayer = null;
        this.vectorWayPointLayer = null;
        this.arrowLayer = null;
        this.compassLayer = null;
        this.route;
        this.arrow = new Arrow();
        this.coveredRoute;
        this.routeLayer;
        if(params){
            this.color = params.color;
        }else {
            this.color = "#C1D1A3";
        }

        this.locationMarkerstyle = new Style({
            fill: new Fill({
              color: 'rgba(0, 0, 255, 0.2)'
            }),
            image: new Icon({
              src: "public/images/direction.svg",
              rotateWithView: true
            })
        });
    }

    // initialize plugin
    init() {
        document.getElementById("beco-container").style.backgroundColor = this.color;

        //$("#beco-container").css("background-color","red");
        var markerStyle = this.markers.style();
        var labelStyle = this.labels.style();


        this.vectorMarkerLayer = new VectorLayer({
            title : "Markers",
            displayInLayerSwitcher: false,
            zIndex : 2,
            source: new VectorSource({
                features: []
            }),
            style: function(feature, resolution) {
                markerStyle.setImage(new Icon(({
                        //color: '#df4a43',
                        scale: 1,
                        //crossOrigin: null,
                        //src: 'public/images/dot.png',
                        src: Image.getImage(feature.get('icon')),
                        anchor :[0.5,0.5],
                        anchorOrigin:'top-left'
                    }))
                );
                markerStyle.getText().setFill(new Fill({ color: feature.get('category_color') }));
                if(resolution < 2){
                    markerStyle.getText().setText(feature.get('name'));
                }else{
                    markerStyle.getText().setText("");
                }
                
                return markerStyle;
            }
        });

        this.vectorWayPointLayer = new VectorLayer({
            title : "WayPointLayer",
            displayInLayerSwitcher: false,
            zIndex : 2,
            source: new VectorSource({
                features: []
            }),
            style: function(feature, resolution) {
                return _this.route.intermediatePointStyle(feature.get('name'));
            }
        });


        this.arrowLayer = new VectorLayer({
            title : "ArrowLayer",
            displayInLayerSwitcher: false,
            zIndex : 2,
            source: new VectorSource({
                features: []
            }),
            style: function(feature, resolution) {
                return _this.arrow.style(feature);
            }
        });


        this.vectorMarkerLayer.getSource().on( 'addfeature', function (ft) {
                // ft - feature being added
               //TODO Feature add
        });

        this.vectorLabelLayer = new VectorLayer({
            title : "Labels",
            displayInLayerSwitcher: false,
            declutter: true,
            source: new VectorSource({
                features: []
            }),
            style: function(feature) {
                labelStyle.getText().setText(feature.get('name'));
                return labelStyle;
            }
        });



        this.compassLayer = new VectorLayer({
            title : "Compass",
            zIndex : 2.5,
            displayInLayerSwitcher: false,
            declutter: true,
            source: new VectorSource({
                features: []
            })
        });

        this.compassLayer.setStyle(this.locationMarkerstyle);
        
    	this.map = new Map({
                layers : this.getLayers(),
		        target: target,
		        view: view,
        		controls: this.showButtons(),
      	});


        this.floors.setInitialFloorTile();
        this.route = new Route(this.map);
        var _this = this;
        this.vectorRouteLayer = new VectorLayer({
            title : "Route Layer",
            displayInLayerSwitcher: false,
            declutter: true,
            zIndex : 1,
            source: new VectorSource({
                features: []
            }),
            style: function(feature, resolution) {
                if(feature.getProperties() && feature.getProperties().preview){
                    return _this.route.previewStyle(feature);
                    
                }else{
                    return _this.route.style(feature, resolution);
                }
                
            }
        });
        this.map.addLayer(this.vectorRouteLayer);
        this.map.addInteraction(this.markers.getSelectFeature(this.vectorMarkerLayer));

        this.map.once('rendercomplete', function(event) {
            //console.log(event);
            window.onRenderComplete();
        });
        
    }


    getLayers() {
        var tileSource = Object.assign([],this.floors.getTileSource());
        tileSource.push(
                    this.vectorMarkerLayer,
                    this.vectorLabelLayer,
                    this.compassLayer,
                    this.vectorWayPointLayer,
                    this.arrowLayer
        );

        return tileSource;
    }

    getMapTileLabels(){
        var layerArray = this.map.getLayers().getArray();
        var mapTile = [];
        _(layerArray).each(function (layer) {
            if(layer.getProperties().displayInLayerSwitcher){
                mapTile.push(layer.getProperties());
            }
        });
        return mapTile;
    }

    showButtons() {
    	var control = defaultControls({
	        attributionOptions:  ({
	            collapsible: false
	        })
    	});
    	return control;
    }

    getMap() {
    	return this.map;
    }


    addLabels(){
        this.vectorLabelLayer.getSource().addFeatures(this.labels.getFeature());
    }

    addMarkers(geojsonObject){
        this.vectorMarkerLayer.getSource().clear();
        this.markers.clearSelectedPoint();
        this.vectorMarkerLayer.getSource().addFeatures(this.markers.getFeature(geojsonObject));
    }

    initializeWhereAmI(coordinates){
        this.whereami.init(this.map,coordinates);
    }

    updatePosition(coordinates){
        this.whereami.setCurrentPoint(coordinates,this.map);
    }

    removeCurrentPosition(){
        this.whereami.removeCurrentPoint(this.map);
    }

    showFloorTile(floorID){
        this.floors.showFloorTile(floorID);
    }

    selectPoint(id){
        if(id) this.markers.selectPoint(id,this.map);
    }

    clearSelectedPoint(){
        this.markers.clearSelectedPoint();
    }
    
    drawRoutePreview(previewObj){
        var _this = this;
        var geometry;
        this.vectorRouteLayer.getSource().clear();
        this.vectorWayPointLayer.getSource().clear();
        this.arrowLayer.getSource().clear();
        this.map.getView().rotate(0);
        _(previewObj.route).each(function (route) {
            var feature = new Feature({
                geometry: new LineString(route),
                preview : true

            });

            if(geometry == null){
                geometry = feature.getGeometry();
            }
            _this.vectorRouteLayer.getSource().addFeature(feature);
        });

        _(previewObj.waypoint).each(function (waypoint) {

            var waypoint = new Feature({
                geometry:  new Point(waypoint.point),
                name : waypoint.name
            });

            _this.vectorWayPointLayer.getSource().addFeature(waypoint);
        });

        this.map.getView().fit(geometry, {
            size:_this.map.getSize(),
            duration: 1000,
            padding :[40,40,60,40],
            easing: easeOut
        });


    }

    clearRoutePreview(){
        this.vectorRouteLayer.getSource().clear();
        this.vectorWayPointLayer.getSource().clear();
    }

    
    drawRoute(routeCoordinatesArray){
        var _this = this;
        if(this.routeLayer){
            this.vectorRouteLayer.getSource().removeFeature(this.routeLayer);
        }

        this.routeLayer = new Feature({
            geometry: new LineString(routeCoordinatesArray)
        });
        
        this.vectorRouteLayer.getSource().addFeature(this.routeLayer);

        // this.map.getView().fit(this.routeLayer.getGeometry(), {
        //     size:_this.map.getSize(),
        //     duration: 1000,
        //     padding :[40,40,40,40],
        //     easing: easeOut
        // });
        //this.map.getView().fit(this.routeLayer.getGeometry(), this.map.getSize());
    }

    addArrow(arrowCoordinates){

        if(this.arrowLayer) {
            this.arrowLayer.getSource().clear();
        }

        var arrow = new Feature({
            geometry: new LineString(arrowCoordinates)
        });

        this.arrowLayer.getSource().addFeature(arrow); 
    }

    drawCoveredRoute(routeCoordinatesArray){
        if(this.coveredRoute){
            this.vectorRouteLayer.getSource().removeFeature(this.coveredRoute);
        }
        
        this.coveredRoute = new Feature({
            geometry: new LineString(routeCoordinatesArray),
            routeType : "coveredroute" 
        });
        this.vectorRouteLayer.getSource().addFeature(this.coveredRoute);
    }

    clearRoute(){
        if(this.vectorRouteLayer) {
            this.vectorRouteLayer.getSource().clear();
            this.routeLayer = null;
            this.coveredRoute = null;
        }
        if(this.vectorWayPointLayer) {
            this.vectorWayPointLayer.getSource().clear();
        }

        if(this.arrowLayer) {
            this.arrowLayer.getSource().clear();
        }
    }

    rotate(angle, coordinates){
        var _this = this;
        this.map.getView().animate({
            //center: [x, y],
            duration: 2000,
            rotation:angle,
            easing:easeOut
        });
        //this.map.getView().fit(point.getGeometry(), this.map.getSize());
        // this.map.getView().rotate(angle);

        var visibleRoute = new Feature({
            geometry: new LineString(coordinates)
        });
        
        this.map.getView().fit(visibleRoute.getGeometry().getExtent(), {
            size:_this.map.getSize(),
            duration: 1000,
            padding :[50,50,50,50],
            easing: easeOut
        });
        
    }

    moveToPoint(x, y){
        this.map.getView().animate({
            center: [x, y],
            duration: 2000,
            easing:easeIn
        });
    }

    resetAngle(){
        this.map.getView().animate({
            center: getCenter(extent),
            duration: 2000,
            rotation:0,
            easing:easeIn
        });
    }

    setWhereAmI(x,y){
        var feature = new Feature(
            new Point([x,y])
        );
        this.vectorMarkerLayer.getSource().addFeature(feature);
    }

    setLocationMarker(x,y, rotateAngle){
        this.compassLayer.getSource().clear();
        var feature = new Feature(
                      new Point([x,y])
                  );
        this.compassLayer.getSource().addFeature(feature);
        if(rotateAngle && rotateAngle >= 0){   
            this.locationMarkerstyle.getImage().setRotation(Math.PI / 180 * rotateAngle);
            //this.compassLayer.getSource().refresh();
        }
    }

    changeDirection(rotateAngle){
        if(rotateAngle >= 0){
            this.locationMarkerstyle.getImage().setRotation(Math.PI / 180 * rotateAngle);
            this.compassLayer.getSource().refresh();
        }
    }

    removeCurrentPosition(){
        this.whereami.removeCurrentPoint(this.map);
    }

    removeLocationMarker(){
        if(this.compassLayer) this.compassLayer.getSource().clear();
    }


}

