import _ from 'underscore';
import Feature from 'ol/Feature.js';
import Point from 'ol/geom/Point.js';
import {Icon, Style, Text, Fill, Stroke} from 'ol/style.js';
import GeoJSON from 'ol/format/GeoJSON.js';
import Select from 'ol/interaction/Select.js';
import { singleclick } from 'ol/events/condition.js';

import Image from './images.js';

export default class Markers {

	constructor() {
    	 this.name = 'Markers';
    	 this.points;
    	 this.select;
    	 this.markers = new Map();
         this.selectedPoint;
  	}

  	toString() {
        return this.name;
    }

    setPoints(points){
    	this.points= points;
    }


    style() {

    	return new Style({
            text: new Text({
				offsetY: 20,
				font: 'bold 10px Open Sans,sans-serif',
				fill: new Fill({ color: '#F90F00' }),
				stroke: new Stroke({ color: '#eee', width: 0.5 })
        	})
        });
    }

    styleSelected(category, text, color) {

    	return new Style({
    		image: new Icon(({
				//color: '#df4a43',
				scale: 1,
				crossOrigin: null,
				//src: 'public/images/dot.png',
				src: Image.getImage(category,'big'),
				anchor :[0.5,0.5],
				anchorOrigin:'top-left'
            })),
            text: new Text({
				offsetY: 30,
				text: text,
				font: 'bold 10px Open Sans,sans-serif',
				fill: new Fill({ color: color }),
				stroke: new Stroke({ color: '#eee', width: 0.5 })
        	})
        });
    }


    getFeature(geojsonObject){
        this.markers.clear();
        this.selectedPoint = null;
        var _this = this;
        var features = (new GeoJSON()).readFeatures(geojsonObject);
        _(features).each(function (feature) {
            _this.markers.set(feature.getId(),feature);
        });
    	return features;
    }

    getSelectFeature(layer){
    	var _this = this;
    	if(!this.select){
    		this.select = new Select({
	            condition: singleclick,
	            layers:[layer],
	            hitTolerance : 10,
	            style: function(feature) {
	                return _this.styleSelected(feature.get('icon'),feature.get('name'),feature.get('category_color_selected'));
	            }
        	});

            this.select.on('select', function(evt){
                if(evt.selected.length > 0){
                    window.onPointSelected(evt.selected[0].getId());
                    this.selectedPoint = evt.selected[0].getId();
                }
            });
    	}
    	
        return this.select;
    }

    selectPoint(id, map){
        if(this.markers && this.markers.has(id)){
            var features = this.select.getFeatures();
            features.clear();
            features.push(this.markers.get(id));
            this.selectedPoint = id;
            map.getView().animate({
                    zoom: map.getView().getZoomForResolution(map.getView().getResolution())
                }, {
                    center: this.markers.get(id).getGeometry().getCoordinates()
                }
            );
        }
    }

    clearSelectedPoint(){
        if(this.markers){
            var features = this.select.getFeatures();
            features.clear();
            this.selectedPoint = null;
        }
    }

}
